<template>
    <div class="electricity-container">
        <mt-header title="水费" style="background-color: #25a9e9">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>

        <div class="electricity-cell-container">
            <span>缴费单位：</span>
            <span>汇美广场-绿城物业</span>
        </div>

        <div class="electricity-cell-container">
            <span>缴费户号：</span>
            <span>xx</span>
        </div>

        <div class="electricity-cell-container">
            <span>户名：</span>
            <span>xx</span>
        </div>

        <div class="electricity-cell-container">
            <span>住址信息：</span>
            <span>x栋xx</span>
        </div>

        <div class="electricity-cell-container">
            <span>更新时间：</span>
            <span>xx年xx月xx日</span>
        </div>

        <div class="electricity-cell-container">
            <span>当前吨数：</span>
            <span>0.00（吨）</span>
        </div>

        <div class="electricity-cell-container">
            <span>当前余额：</span>
            <span>0.00（元）</span>
        </div>

        <div class="electricity-cell-container">
            <span>充值金额：</span>
            <input  type="number" placeholder="请输入充值金额"/>
        </div>

        <button class="com-btn-pay">立即充值</button>
    </div>
</template>

<script>
    export default {
        name: "WaterFee"
    }
</script>

<style lang="scss" scoped>

    .electricity-container{
        display: flex;
        flex-direction: column;
        background-color: #ffffff;
        height: 100%;
    }

    .electricity-cell-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin: 0 10px;
        border-bottom: 1px solid #f5f5f5;

    span{
        font-size: 12px;
        color: #808080;
        padding: 10px 0;
    }

    input{
        height: 25px;
        font-size: 12px;
        margin: 10px 0;
        padding: 0 10px;
        width: 55%;
    }
    }

    .com-btn-pay{
        width: 90%;
        display: flex;
        position: fixed;
        bottom: 60px;
        left: 0 ;
        right: 0;
        margin: 0 auto;
        background-color: #25a9e9;
        color: #ffffff;
        padding: 10px 0;
        font-size: 12px;
        justify-content: center;
        border-width: 0;
    }

</style>


