<template>
    <div class="home-container">
        <img class="home-banner" src="../../assets/home_01.png"/>

        <div class="home-notice-container">
            <div class="home-notice-img-container">
                <img src="../../assets/home_02.png"/>
                <span>公告栏</span>
            </div>
            <div class="home-notice-title">汇美广场公告栏-智能水电缴费</div>
        </div>

        <div class="line"></div>

        <div class="home-cate-container" >
            <div  v-for="item in homeCateArr">
                <HomeCateItem v-bind:cate-data="item"></HomeCateItem>
            </div>
        </div>

        <div class="line"></div>

        <div style="color: #000000;font-size: 14px;text-align: center;margin: 10px 0">
            缴费栏目
        </div>

        <div class="home-tools-container">
            <div>
                <img src="../../assets/home_06.png" @click="onClick(1)" >
            </div>
            <div>
                <img src="../../assets/home_07.png" @click="onClick(2)" >
            </div>

        </div>

        <div class="home-tools-container">
            <div>
                <img src="../../assets/home_08.png" @click="onClick(1)" >
            </div>
            <div>
                <img src="../../assets/home_09.png" @click="onClick(2)" >
            </div>
        </div>

    </div>
</template>

<script >
    import { MessageBox } from 'mint-ui';
    import HomeCateItem from "../item/HomeCateItem.vue"

    const catePath_01=require("../../assets/home_03.png")
    const catePath_02=require("../../assets/home_04.png")
    const catePath_03=require("../../assets/home_05.png")

    export default {
        components: {HomeCateItem},
        comments:{
            HomeCateItem
        },

        data(){
            return{
                isLogin:this.GLOBAL.isLogin,
                homeCateArr:[
                    {
                        img:catePath_01,
                        title:"房屋信息"
                    },
                    {
                        img:catePath_02,
                        title:"物业报修"
                    },
                    {
                        img:catePath_03,
                        title:"小区服务"
                    }
                ]
            }
        },

        created:function () {
            this.jugdeLogin()
        },

        methods:{
            jugdeLogin: function() {
                if(this.isLogin===false){
                    MessageBox.confirm('尚未登录，请先登录').then(action => {
                        this.GLOBAL.isShowTab=false
                        this.$router.push('/login')
                    });
                }else {
                    alert("登录")
                }
            },
            
            onClick:function (id) {
                console.log(id)
                var path=""
                switch (id) {
                    case 1:
                        path='/electricityFee'
                        break
                    case 2:
                        path='/waterFee'
                        break
                    case 3:
                        path='/electricityFee'
                        break
                    case 4:
                        path='/electricityFee'
                        break
                }
                this.$router.push(path)
            }
        }

    }
</script>

<style lang="scss" scoped>
    .home-container{
        display: flex;
        flex-direction: column;
        height: 100%;
        background-color: #ffffff;
        font-family: "Microsoft YaHei UI";
        margin-bottom: 65px;
    }

    .home-banner{
        width: 100%;
        height: 160px;
        background-size: cover;
    }

    .home-notice-container, .home-notice-img-container{
        display: flex;
        flex-direction: row;
        align-items: center;

        .home-notice-img-container{
            img {
                width: 18px;
                height: 18px;
                margin: 10px;
            }
            span{
                font-size: 12px;
                color: #bcccea;
            }
        }

        .home-notice-title{
            font-size: 12px;
            color: #808080;
            margin: 0 10px;
        }
    }

    .line{
        height: 5px;
        background-color: #f5f5f5;
    }

    .home-cate-container{
        display: flex;
        flex-direction: row;
        margin: 15px 0;
        div{
            display: flex;
            flex-grow: 1;
        }
    }

    .home-tools-container{
        display: flex;
        flex-direction: row;
        align-items: center;

        div{
            display: flex;
            flex-grow: 1;
            align-items: center;
            img{
                width: 85%;
                margin: 10px 10px 0;
            }
        }
    }

</style>



