<template>
     <div class="app-container">

          <router-view></router-view>

          <nav v-if="isShow" class="mui-bar mui-bar-tab">
               <router-link class="mui-tab-item " to="/home">
                    <span class="mui-icon  app-tab-one"></span>
                    <span class="mui-tab-label ">首页</span>
               </router-link>
               <router-link class="mui-tab-item" to="/service">
                    <span class="mui-icon app-tab-two"></span>
                    <span class="mui-tab-label">小区服务</span>
               </router-link>
               <router-link class="mui-tab-item" to="/activities">
                    <span class="mui-icon  app-tab-three"></span>
                    <span class="mui-tab-label">活动中心</span>
               </router-link>
               <router-link class="mui-tab-item" to="/mine">
                    <span class="mui-icon  app-tab-four"></span>
                    <span class="mui-tab-label">我的</span>
               </router-link>
          </nav>

     </div>
</template>

<script>
    export default {
        data(){
            return{
                isShow:true
            }
        },

        created: function(){
            this.isShow=this.GLOBAL.isShowTab
        },

        updated:function () {
            this.isShow=this.GLOBAL.isShowTab
        },

    }
</script>

<style  scoped>
     .app-container{
          height: 100%;
     }

     .app-tab-one{
          background: url("../assets/tab_02.png") no-repeat;
          background-size: cover;
     }

     .app-tab-two{
          background: url("../assets/tab_04.png") no-repeat;
          background-size: cover;
     }
     .app-tab-three{
          background: url("../assets/tab_06.png") no-repeat;
          background-size: cover;
     }
     .app-tab-four{
          background: url("../assets/tab_08.png") no-repeat;
          background-size: cover;
     }

     .mui-bar-tab .mui-tab-item.mui-active .app-tab-one{
          background-image: url("../assets/tab_01.png");
          background-size:cover;
     }

     .mui-bar-tab .mui-tab-item.mui-active .app-tab-two{
          background-image: url("../assets/tab_03.png");
          background-size:cover;
     }
     .mui-bar-tab .mui-tab-item.mui-active .app-tab-three{
          background-image: url("../assets/tab_05.png");
          background-size:cover;
     }
     .mui-bar-tab .mui-tab-item.mui-active .app-tab-four{
          background-image: url("../assets/tab_07.png");
          background-size:cover;
     }

     .mui-bar-tab .mui-tab-item.mui-active{
          color: #25a9e9;
     }

</style>



