<template>
    <div class="electricity-container">
        <mt-header title="电费" style="background-color: #25a9e9">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>

        <div class="electricity-cell-container">
            <span>缴费单位：</span>
            <span>汇美广场-绿城物业</span>
        </div>

        <div class="electricity-cell-container">
            <span>缴费户号：</span>
            <span>xx</span>
        </div>

        <div class="electricity-cell-container">
            <span>户名：</span>
            <span>xx</span>
        </div>

        <div class="electricity-cell-container" @click="popupVisible=true">
            <span>住址信息：</span>
            <span>x栋xx</span>
        </div>

        <div class="electricity-cell-container">
            <span>更新时间：</span>
            <span>xx年xx月xx日</span>
        </div>

        <div class="electricity-cell-container">
            <span>当前度数：</span>
            <span>0.00（度）</span>
        </div>

        <div class="electricity-cell-container">
            <span>当前余额：</span>
            <span>0.00（元）</span>
        </div>

        <div class="electricity-cell-container">
            <span>充值金额：</span>
            <input  type="number" placeholder="请输入充值金额"/>
        </div>

        <mt-popup v-model="popupVisible" position="bottom" style="width: 100%">
            <mt-picker :slots="slots" @change="selectHouseInfo" ></mt-picker>
        </mt-popup>

        <button class="com-btn-pay">立即充值</button>
    </div>

</template>

<script>

    export default {
        data(){
            return{
                popupVisible:false,
                slots: [
                    {
                        values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                    }
                ]

            }
        },
        methods:{
             selectHouseInfo:function (picker, values) {
                 picker.setSlotValue(1, values[0]);
             }
        }
    }
</script>

<style lang="scss" scoped>

    .electricity-container{
        display: flex;
        flex-direction: column;
        background-color: #ffffff;
        height: 100%;
    }

    .electricity-cell-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin: 0 10px;
        border-bottom: 1px solid #f5f5f5;

        span{
            font-size: 12px;
            color: #808080;
            padding: 10px 0;
        }

        input{
            height: 25px;
            font-size: 12px;
            margin: 10px 0;
            padding: 0 10px;
            width: 55%;
        }
    }

    .com-btn-pay{
        width: 90%;
        display: flex;
        position: fixed;
        bottom: 60px;
        left: 0 ;
        right: 0;
        margin: 0 auto;
        background-color: #25a9e9;
        color: #ffffff;
        padding: 10px 0;
        font-size: 12px;
        justify-content: center;
        border-width: 0;
    }

</style>


